.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #C2C2C2;
  font-weight: 600;
  text-transform: uppercase;
  overflow: auto;

  &__top {
    margin-bottom: 32px;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    color: $second-text-color;
    text-decoration: none;
    font-size: 16px;
    line-height: 27px;
  }

  &__book-test-ride {
    display: block;
    padding-bottom: 7px;
    border-bottom: 1px solid $second-text-color;
    width: fit-content;

    color: $second-text-color;
    text-decoration: none;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
  }
}
